.details_page_banner{
    display: flex;
    position: relative;
    margin-bottom: 115px;
}
.text_wrapper{
    font-family: "Roboto", sans-serif;
   flex-grow: 1;
   position: relative;
   left: 130px;
    top: 250px;
    height: fit-content;
}
.text_wrapper p{
    position: absolute;
    color: #fff;
    background-color: #7a1655;
    font-size: 67px;
    padding: 90px 30px 70px 90px;
    width: 145%;
    font-weight: 600;
    margin-top: 21px;
    left: 4px;
    line-height: 90px;
    font-family: 'IBM Plex Sans';
}
.text_wrapper h1{
    color: #7a1655;
    font-size: 93px;
    font-weight: 400;
    font-family: 'IBM Plex Sans';
}
.text_wrapper h2{
    position: absolute;
    font-size: 64px;
    font-weight: 400;
    top: 470px;
    margin-top: 45px;
}
.thinner{
    font-weight: 200;
}
.image_wrapper{
    width: 58%;
    height: 125%;
}