*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  img{
    width:100%;
    height: 100%;
  }
  
  .header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px 80px 35px;
  }
  .header-menu{
    color: #7a1655;
    font-weight: 600;
    font-size: 19px;
    display: flex;
    justify-content: space-between;
    width:22.6%;
  }
  .header-menu div{
    cursor: pointer;
  }
  .header-logo{
    width: 8.5%;
    cursor: pointer;
  }