.explore_wrapper{
    display: flex;
    flex-direction: row;
    margin: 0px 0px 105px 0px ;
  }
  .explore_form{
    display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 590px;
      z-index: 1;
    flex-grow: 1;
    padding: 0 90px;
    position: relative;
    left: 80px;
    top: 191px;
  
  }
  .explore_form select{
    border: 2px solid;
    margin-bottom: 23px;
  }
  .explore_banner{
    width: 57.3%;
    height: 120%;
  }
  .explore_form h2 {
    color: #791655;
    font-weight: 500;
    opacity: 0.7;
    font-family: 'Roboto';
  }
  .explore_form h1 {
    color: #791655;
    font-size: 85px;
      font-weight: 250;
      margin-top: 35px;
      margin-bottom: 85px;
      font-family: 'Roboto';
      font-weight: 300;
  }
  .bolder{
    font-weight: 500;
  }