.article{
    width: 31%;
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    align-content: space-between;
    font-family: 'IBM Plex Sans', sans-serif;
  }
  .image {
    width: 100%;
    height: 395px;
  }
  .subtitle {
    font-weight: 300;
    font-size: 40px;
    margin-top: 16px;
  }
  .place{
    padding-top: 10px;
    font-size: 22px;
    font-weight: 900;
  }
  .content {
    padding: 30px 0;
    font-size: 21px;
    font-weight: 400;
    line-height: 1.3;
    text-align: justify;
  }