
.fullDescription{
    text-align: justify;
    font-size: 35px;
    line-height: 1.5;
    margin-bottom: 105px;
}
.fullDescription p{
    margin-bottom: 42px;
}
.place_container{
    width: 86%;
    margin: 0 auto 115px;
}
.bigger,.big,.fullDescription{
    font-family: "Roboto", sans-serif;
}
.bigger,.big{
    color: #7a1655;
}
.bigger{
    font-size: 54px;
  font-weight: 500;
  letter-spacing: 1px;
}
.big{
    font-size: 26.5px;
    opacity: 0.6;
    margin-top: 6px;
}
.places_content{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 100px;
    column-gap: 33px;
    margin: 50px auto 100px;
    font-family: "Roboto", sans-serif;
}